.app-wrapper {
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
            "h h"
            "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.background {
    height: 400px;
    width: 100%;
}

.app-wrapper-content {
    grid-area: c;
    background-color: cornsilk;
}